/* NAV */
nav {
	position:relative;
	background:#333;
	color: #f1f1f1;
	display:inline-block;
	margin-top:1em;
	padding:.25em 0;
}
nav Link {
  /*
	color:#f1f1f1;
	display:inline-block;
  padding:.5em 1em;*/
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}
nav label {
	border: .15em solid #444;
	border-radius: 2px;
	margin-left: 2em;
}
nav label:hover {
  /*background: #444;*/
  background-color: #ddd;
  color: black;
}
nav li, nav label {
	display:inline-block;
	-moz-transition:background .3s;
	-webkit-transition:background .3s;
	transition:background .3s;
}
#nav-input {
	display: none;
}
@media all and (min-width: 48em) {
	nav:before {
		position:absolute;
		top:0; right:100%; bottom:0;
		border:1.33em solid #333;
		border-left-color:transparent;
		content:"";
	}
	nav li {
		margin-right:2rem;
	}
	nav li:hover {
		background: #e2f105;
	}
		#nav-unicode, label[for="nav-input"] {
			display: none;
		}
}
@media all and (max-width: 48em) {
	nav {
		text-align: left;
		width: 100%;
	}
	nav.fixed {
		text-align:left;
	}
	nav.fixed ul {
		box-shadow: none;
	}
	nav Link {
		width: 100%;
	}

	nav li {

		text-align: center;
		width: 100%;
	}
	nav li:hover {
		background:#009b91;
	}
		#nav-unicode, label[for="nav-input"] {
			cursor: pointer;
			display: inline-block;
		}
		label[for="nav-input"] {
			padding: .5em 1em;
		}
		#nav-input + ul {
			display: none;
		}
		#nav-input:checked + ul {
			position: absolute;
			top: 3.25em; left: 0; right: 0;
			border-top: .2em solid rgba(51,51,51,.3);
			box-shadow:0 .2em rgba(51,51,51,.3);
			display: block;
			text-shadow: 0 1px rgba(51,51,51,.3);
			z-index: 9;
		}
}
